.navbar-expand-lg .navbar-collapse{
    justify-content: center;
    justify-items: center;
    text-align: center;
    background-color: transparent;

    @media (min-width: 320px) and (max-width:425px){
        font-size: 14px;
        font-weight: bold;
    }


}

.navbar-brand{

    img{
        width: 50px;
    }
}
.nav-item{
    a{
        font-family: 'Quicksand', sans-serif;
        font-size: 16px;
        color: $default;
        &:hover{
            color: #01a3a4;
        }
    }

}

